import {useState} from "react";

function Todo()
{
    const [todoList,setTodoList]=useState([]);
    const [todoItem,setTodoItem]=useState();
    const inputChanged=(e)=>{
        setTodoItem(e.target.value);
        console.log(todoItem);
    };

    const addBtnClicked=(e)=>{
        console.log(todoList);
        console.log(todoItem);
        setTodoList([...todoList,{value:todoItem,key:crypto.randomUUID()}]);
        console.log(todoList);
    };

    return (<div>
        <input onChange={(e)=>{inputChanged(e);}}/>
        <button onClick={(e)=>{addBtnClicked(e);}}>Add</button>
        <div>
            <ul>
                {todoList.map((item)=>{return (<li key={item.key}>{item.value}</li>);})}
            </ul>
        </div>
    </div>);
}

export default Todo;